a.navLink {
  position: relative;
}

a.navLink::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    border-radius: 4px;
    background-color: #cbd5e1;
    bottom: 0;
    left: 0;
    transform-origin: right;
    transform: scaleX(0);
    transition: transform .3s ease-in-out;
  }

a.navLink:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}
